import { Loc } from "./types";

export const calculateDistance = (loc1: Loc, loc2: Loc) => {
  const lat1 = loc1.lat;
  const lon1 = loc1.lng;
  const lat2 = loc2.lat;
  const lon2 = loc2.lng;
  const R = 3958.8; // Earth's radius in miles
  const toRadians = (degrees: number) => degrees * Math.PI / 180;
  const dLat = toRadians(lat2 - lat1);
  const dLon = toRadians(lon2 - lon1);
  const a =
    Math.sin(dLat / 2) * Math.sin(dLat / 2) +
    Math.cos(toRadians(lat1)) * Math.cos(toRadians(lat2)) *
    Math.sin(dLon / 2) * Math.sin(dLon / 2);
  const c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1 - a));
  const distance = R * c; // Distance in miles
  return distance;
}

export const formatCalculateDistance = (loc1: Loc, loc2: Loc) => {
  const distMiles = calculateDistance(loc1, loc2);
  const distFeet = distMiles * 5280;
  if (distFeet < 1000) {
    return `${Math.round(distFeet)} ft`;
  }
  return `${distMiles.toFixed(2)} miles`;
};

export const locsEqual = (loc1: Loc, loc2: Loc) => {
  return loc1.lat === loc2.lat && loc1.lng === loc2.lng;
};

export const latLngToLoc = (latLng: google.maps.LatLng) => {
  return { lat: latLng.lat(), lng: latLng.lng() } as Loc;
};

export const locToLatLng = (loc: Loc) => {
  return new google.maps.LatLng(loc.lat, loc.lng);
};