import React, { useCallback, useEffect, useState } from "react";
import ListView from "./components/list";
import MapView from "./components/map";
import { findAllStations } from "./lib/stations";
import { Station, MoveMode } from "./lib/types";
import { Loc } from "./lib/types";
import { calculateDistance, latLngToLoc, locToLatLng, locsEqual } from "./lib/loc";
import { APIProvider, useMap } from "@vis.gl/react-google-maps";
import { getCurrentLocationSync } from "./lib/geolocate";
import { Box, Container, LinearProgress, Stack, ToggleButton, ToggleButtonGroup } from "@mui/material";
import DoNotDisturbIcon from '@mui/icons-material/DoNotDisturb';

const locationUpdatePeriodMillis = 5 * 1000;
const requestUpdatePeriodMillis = 60 * 1000;
const stationsLimit = 20;

type Setter<T = any> = React.Dispatch<React.SetStateAction<T>>;

interface MapStateCtrlProps {
  requestCurrentLocation: () => Promise<Loc>;
  isInFollowMode: boolean;
  mapRef: React.MutableRefObject<google.maps.Map | undefined>;
  setCurrentLocation: Setter<Loc | undefined>;
}

const MapStateCtrl: React.FC<MapStateCtrlProps> = ({
  requestCurrentLocation,
  isInFollowMode,
  mapRef,
  setCurrentLocation }) => {

  const map = useMap();

  useEffect(() => {
    if (!map) return;
    if (mapRef.current) return;

    mapRef.current = map;

    console.log("MapStateCtrl", map, mapRef);

    map.addListener("idle", () => {
      const center = map.getCenter();
      console.log("idle", "center", center);
      if (center) {
        setCurrentLocation({ lat: center.lat(), lng: center.lng() } as Loc);
      }
    });
  }, [map, mapRef, setCurrentLocation]);

  useEffect(() => {
    if (!map) return;
    if (!isInFollowMode) return;
    const setLocation = async () => {
      const loc = await requestCurrentLocation();
      console.log("MapStateCtrl setting center from currentLocation", loc);
      const center = map.getCenter();
      if (center && locsEqual(latLngToLoc(center), loc)) {
        console.log("MapStateCtrl setting center already set", loc);
        return;
      }
      setCurrentLocation(loc);
      map.panTo(loc);
    }
    setLocation();
  }, [map, isInFollowMode, requestCurrentLocation]);

  return <></>;
}
interface SettingsProps {
  isLoading: boolean;
  moveMode: MoveMode;
  setMoveMode: (mode: MoveMode) => void;
}
const Settings: React.FC<SettingsProps> = ({
  isLoading,
  moveMode,
  setMoveMode,
}) => {
  const handleChange = (
    event: React.MouseEvent<HTMLElement>,
    newMoveMOde: string | null,
  ) => {
    setMoveMode(newMoveMOde as MoveMode);
  };

  return <>
    < Box sx={{ width: "100%", p: 0, minHeight: 5 }}>
      {isLoading && <LinearProgress />}
    </Box >
    <Stack spacing={0} alignItems="center">
      <ToggleButtonGroup
        exclusive
        size="small"
        value={moveMode}
        onChange={handleChange}
      >
        <ToggleButton value="none" key="none">
          none
        </ToggleButton>
        <ToggleButton value="follow" key="follow">
          follow
        </ToggleButton>
        <ToggleButton value="manual" key="manual">
          manual
        </ToggleButton>
      </ToggleButtonGroup>
    </Stack >
  </>;
}

const App: React.FC = () => {
  const [stationsData, setStationsData] = useState<Station[] | undefined>();
  const [initialLocation, setInitialLocation] = useState<Loc | undefined>();
  const [moveMode, setMoveMode] = useState<MoveMode>("follow" as MoveMode);
  const [currentLocationFromLocationUpdate, setCurrentLocationFromLocationUpdate] = useState<Loc | undefined>();
  const [currentLocation, setCurrentLocation] = useState<Loc | undefined>();
  const [isRequestingStations, setIsRequestingStations] = useState<boolean>(false);
  const [isRequestingCurrentLocation, setIsRequestingCurrentLocation] = useState<boolean>(false);
  const mapRef = React.useRef<google.maps.Map | undefined>();

  const setCenterFromList = useCallback((loc: Loc) => {
    console.log("setCenterFromList SETTING", loc);
    if (mapRef.current) {
      mapRef.current.panTo(loc);
    }
  }, []);

  const setCenterFromLocationUpdate = useCallback((loc: Loc) => {
    console.log("setCenterFromLocationUpdate", loc);
    setCurrentLocationFromLocationUpdate(prev => prev && locsEqual(prev, loc) ? prev : loc);
    if (moveMode !== "follow") {
      console.log("setCenterFromLocationUpdate not in follow mode");
    } else {
      setCurrentLocation(prev => prev && locsEqual(prev, loc) ? prev : loc);
      console.log("setCenterFromLocationUpdate SETTING", loc);
    }
  }, [moveMode]);

  useEffect(() => {
    const requestStations = async () => {
      console.log("requestStations");
      setIsRequestingStations(true);
      const stations = await findAllStations();
      setStationsData(stations);
      setIsRequestingStations(false);
      // const currentLocation = findCenter(stations);
      // setInitialLocation(prev => prev || currentLocation);
    }
    requestStations();
    const id = setInterval(async () => {
      await requestStations();
    }, requestUpdatePeriodMillis);

    return () => clearInterval(id);
  }, []);

  useEffect(() => {
    const requestCurrentLocation = async () => {
      setIsRequestingCurrentLocation(true);
      const loc = await getCurrentLocationSync();
      setCenterFromLocationUpdate(loc);
      setInitialLocation(prev => prev || loc);
      setIsRequestingCurrentLocation(false); // This isn"t really
    }
    requestCurrentLocation();
    const id = setInterval(async () => {
      await requestCurrentLocation();
    }, locationUpdatePeriodMillis);

    return () => clearInterval(id);
  }, [setCenterFromLocationUpdate]);

  const getOrRequestCurrentLocation = async () => {
    if (currentLocationFromLocationUpdate) {
      return currentLocationFromLocationUpdate;
    }
    const res = await getCurrentLocationSync();
    setCurrentLocationFromLocationUpdate(res);
    return res;
  };

  const isLoading = isRequestingStations || isRequestingCurrentLocation;

  const sortedStations = !currentLocation ? stationsData : stationsData?.sort((a, b) => calculateDistance(a.location, currentLocation) - calculateDistance(b.location, currentLocation));
  const prunedStations = !sortedStations ? [] : sortedStations.slice(0, stationsLimit);

  return (
    <APIProvider apiKey={process.env.REACT_APP_GOOGLE_MAPS_API_KEY as string}>
      <MapStateCtrl
        mapRef={mapRef}
        requestCurrentLocation={getOrRequestCurrentLocation}
        setCurrentLocation={setCurrentLocation}
        isInFollowMode={moveMode !== "follow"}
      />
      <Container fixed disableGutters>
        <Box
          sx={{ p: 0, m: 0, width: "100%", height: "100%", overflow: "none" }}>
          <Box sx={{ position: "absolute", left: 0, top: 0, width: "100%", height: "45%" }}>
            {
              prunedStations && initialLocation &&
              <MapView
                moveMode={moveMode}
                stations={prunedStations}
                initialLocation={initialLocation}
                setMoveMode={setMoveMode}
                currentLocation={currentLocationFromLocationUpdate}
              />}
          </Box>
          <Box sx={{ position: "absolute", left: 0, top: "45%", width: "100%", height: "6%" }}>
            <Settings
              isLoading={isLoading}
              moveMode={moveMode}
              setMoveMode={setMoveMode}
            />
          </Box>
          <Box sx={{ px: 2, position: "absolute", left: 0, top: "51%", width: "100%", height: "49%" }}>
            {
              prunedStations && currentLocation &&
              <ListView
                setCenter={setCenterFromList}
                stations={prunedStations}
                currentLocation={currentLocation} />}
          </Box>
        </Box>
      </Container>
    </APIProvider>
  );
};

export default App;
