import { Station } from "./types";

interface GetHighlightColorScale<C extends string> {
  high: C;
  medium: C;
  low: C;
}

interface GetHighlightColorProps<C extends string> {
  station: Station;
  scale: GetHighlightColorScale<C>;
}

export const getHighlightColor = <C extends string>(props: GetHighlightColorProps<C>) => {
  const { station, scale } = props;
  const { high, medium, low } = scale;
  return station.numEBikes > 5
    ? high : station.numEBikes > 0
      ? medium : low;
};