import { formatCalculateDistance, calculateDistance } from "../lib/loc";
import { Station, Loc } from "../lib/types";
import { Avatar, Badge, Chip, Divider, List, ListItem, ListItemButton, ListItemIcon, ListItemText, Stack, Typography } from "@mui/material";
import ElectricBikeIcon from '@mui/icons-material/ElectricBike';
import PedalBikeIcon from '@mui/icons-material/PedalBike';
import { getHighlightColor } from "../lib/colors";
import * as hacks from "../lib/hacks";
import { blue, purple } from "@mui/material/colors";

interface TableViewProps {
  stations: Station[];
  currentLocation: Loc;
  setCenter: (loc: Loc) => void;
}

const legacy = false;

const StationGlyph = ({ station }: { station: Station }) => {
  const { numEBikes, numBikes } = station;

  return <Stack direction="column" spacing={1}>
    {/* <Chip label={ */}
    <Stack direction="row" spacing={2}>
      <Badge badgeContent={numEBikes} color="primary">
        <Avatar sx={{ bgcolor: blue[900], width: 24, height: 24 }}>
          <ElectricBikeIcon fontSize="small" />
        </Avatar>
      </Badge>
      <Badge badgeContent={numBikes} color="secondary">
        <Avatar sx={{ bgcolor: purple[900], width: 24, height: 24 }}>
          <PedalBikeIcon fontSize="small" />
        </Avatar>
      </Badge>
    </Stack>
  </Stack>
};

const ListView: React.FC<TableViewProps> = ({ stations, currentLocation, setCenter }) => {
  const sortedStations = stations.sort((a, b) => calculateDistance(a.location, currentLocation) - calculateDistance(b.location, currentLocation));
  return <List
    sx={{
      py: 0,
      width: '100%',
      maxWidth: 360,
      overflow: "auto",
      maxHeight: hacks.listHeight,
      borderRadius: 2,
      border: '1px solid',
      borderColor: 'divider',
      backgroundColor: 'background.paper',
    }}>
    {sortedStations.map((station, index) => {
      const color = getHighlightColor({
        station,
        scale: { high: "success", medium: "primary", low: "error" },
      });
      const onClick = () => {
        console.log("onClick", station);
        setCenter(station.location);
      };
      const dist = `${formatCalculateDistance(station.location, currentLocation)} away`;
      if (legacy) {
        return <>
          <ListItem key={`list-item-${station.id}`}>
            <ListItemButton onClick={onClick}>
              <ListItemIcon>
                <Badge badgeContent={station.numEBikes} color={color}>
                  <ElectricBikeIcon />
                </Badge>
              </ListItemIcon>
              <ListItemText
                primary={station.name}
                secondary={dist} />
            </ListItemButton>
          </ListItem>
          {index < sortedStations.length - 1 &&
            <Divider key={`divider-${station.id}`} component="li" />}
        </>;
      }
      return <>
        <ListItem key={`list-item-${station.id}`}>
          <ListItemButton onClick={onClick}>
            <ListItemText
              primary={station.name}
              secondary={dist} />
            <StationGlyph station={station} />
          </ListItemButton>
        </ListItem>
        {index < sortedStations.length - 1 &&
          <Divider key={`divider-${station.id}`} component="li" />}
      </>;
    })}
  </List>;
}

export default ListView;
