import { Station, Loc, MoveMode } from "../lib/types";
import { Map, AdvancedMarker, useMap, Pin, MapControl, ControlPosition } from '@vis.gl/react-google-maps';
import { getHighlightColor } from "../lib/colors";
import FollowTheSignsIcon from '@mui/icons-material/FollowTheSigns';
import { Box, Paper, FormControlLabel, Switch, Stack, Grid, IconButton } from "@mui/material";
import { useCallback, useEffect, useMemo, useState } from "react";
import { locToLatLng } from "../lib/loc";
import ArrowCircleLeftIcon from '@mui/icons-material/ArrowCircleLeftOutlined';
import ArrowCircleRightIcon from '@mui/icons-material/ArrowCircleRightOutlined';
import ArrowCircleUpIcon from '@mui/icons-material/ArrowCircleUpOutlined';
import ArrowCircleDownIcon from '@mui/icons-material/ArrowCircleDownOutlined';
import ReplayIcon from '@mui/icons-material/Replay';

interface MapViewProps {
  stations: Station[];
  initialLocation: Loc;
  currentLocation: Loc | undefined;
  setMoveMode: (mode: MoveMode) => void;
  moveMode: MoveMode;
}

const debug = true;

const MapView = ({ stations, initialLocation, setMoveMode, moveMode, currentLocation }: MapViewProps) => {
  const map = useMap();

  // useEffect(() => {
  //   setMoveMode(moveMode);
  // }, [setMoveMode, moveMode])

  const DebugCtrl = () => {
    const moveDir = useCallback(({ dx = 0, dy = 0 }: { dx?: number, dy?: number }) => {
      if (moveMode !== "manual") return;

      if (!map) return;
      const bounds = map.getBounds();
      if (!bounds) return;
      const center = bounds?.getCenter();
      if (!center) return;
      const ne = bounds?.getNorthEast();
      if (!ne) return;
      const sw = bounds?.getSouthWest();
      if (!sw) return;

      const width = ne?.lng() - sw?.lng();
      const height = ne?.lat() - sw?.lat();
      console.log("width", width, "height", height);

      const x = dx * width / 4;
      const y = dy * height / 4;
      console.log("panBy", "x", x, "y", y);
      map.panBy(x, y);

    }, []);
    return <Paper sx={{ p: 1, alignContent: "center", verticalAlign: "middle" }}>
      <Grid container spacing={0}>
        <Grid item xs={4}>

        </Grid>
        <Grid item xs={4}>
          <IconButton onClick={() => moveDir({ dy: -1 })}>
            <ArrowCircleUpIcon />
          </IconButton>
        </Grid>
        <Grid item xs={4}>

        </Grid>
        <Grid item xs={4}>
          <IconButton onClick={() => moveDir({ dx: -1 })}>
            <ArrowCircleLeftIcon />
          </IconButton>
        </Grid>
        <Grid item xs={4}>
          <ReplayIcon />
        </Grid>
        <Grid item xs={4}>
          <IconButton onClick={() => moveDir({ dx: 1 })}>
            <ArrowCircleRightIcon />
          </IconButton>
        </Grid>
        <Grid item xs={4}>

        </Grid>
        <Grid item xs={4}>
          <IconButton onClick={() => moveDir({ dy: 1 })}>
            <ArrowCircleDownIcon />
          </IconButton>
        </Grid>
        <Grid item xs={4}>

        </Grid>
      </Grid>
    </Paper>;
  }

  const maxNumEBikes = useMemo(() =>
    stations.reduce((acc, station) => Math.max(acc, station.numEBikes), 0)
    , [stations]);

  let img = document.getElementById("glyph-img") as HTMLImageElement;
  if (!img) {
    img = document.createElement("img");
    img.style.width = "80px";
    img.style.height = "80px";
    img.id = "glyph-img";
    img.src = "/glyph.png";
  }
  const background = "rgb(255 255 255 / 0%)";
  const you = map && currentLocation &&
    <AdvancedMarker
      title={"You"}
      position={locToLatLng(currentLocation)}>
      <Pin
        background={background}
        borderColor={background}
        glyphColor={background}
        glyph={img}
      />
    </AdvancedMarker>;

  return <Map
    mapTypeControl={false}
    streetViewControl={false}
    mapId="map"
    defaultCenter={initialLocation}
    defaultZoom={17} >
    {debug &&
      <MapControl position={ControlPosition.TOP_LEFT}>
        <Box sx={{ p: 1.5 }}>
          <DebugCtrl />
        </Box>
      </MapControl>
    }
    {you}
    {stations.length && map && stations.map((station, index) => {
      const background = getHighlightColor({
        station,
        scale: { high: "#2e7d32", medium: "#1976d2", low: "#d32f2f" },
      });
      const scale = 1.5 * station.numEBikes / maxNumEBikes + .5;
      return <AdvancedMarker
        key={index}
        title={station.numEBikes.toString()}
        position={{ lat: station.location.lat, lng: station.location.lng }}
      >
        <Pin
          background={background}
          borderColor={background}
          glyphColor={background}
          glyph={String(station.numEBikes)}
          scale={scale}
        />
      </AdvancedMarker>
    })}
  </Map >;
};

export default MapView;