import { Station, Loc } from "./types";

export const findAllStations = async (): Promise<Station[]> => {
  const getRawStationStatuses = async () => {
    const resp = await fetch("https://gbfs.lyft.com/gbfs/2.3/bkn/en/station_status.json");
    const data = await resp.json() as any;
    const { data: { stations } } = data;
    return stations;
  };

  const findInLocalStorage = () => {
    const stationsStr = localStorage.getItem("stations");
    if (!stationsStr) {
      return;
    }
    try {
      return JSON.parse(stationsStr);
    } catch (e) {
      console.error(e);
      localStorage.removeItem("stations");
    }
  };

  const getRawStationInfos = async () => {
    const stored = findInLocalStorage();
    if (stored) {
      return stored;
    }
    const resp = await fetch("https://gbfs.lyft.com/gbfs/2.3/bkn/en/station_information.json");
    const data = await resp.json() as any;
    const { data: { stations } } = data;
    localStorage.setItem("stations", JSON.stringify(stations));
    return stations;
  };

  const joinStations = (statuses: any[], infos: any[]): any[] => statuses.map(s => {
    const info = infos.find(i => i.station_id === s.station_id);
    return { ...s, ...info } as any;
  });

  const [statuses, infos] = await Promise.all([getRawStationStatuses(), getRawStationInfos()]);
  const joined = joinStations(statuses, infos);
  const stations = joined.map((s: any) => {
    const {
      capacity, short_name, name,
      lat, lon, station_id, num_ebikes_available, num_bikes_available,
    } = s;
    return {
      id: String(station_id),
      capacity: Number(capacity),
      shortName: String(short_name),
      name: String(name),
      location: { lat: Number(lat), lng: Number(lon) },
      numEBikes: Number(num_ebikes_available),
      numBikes: Number(num_bikes_available),
    } as Station;
  });
  return stations;
};

export const findCenter = (stations: Station[]) => {
  const avgLat = stations.reduce((acc, station) => acc + station.location.lat, 0) / stations.length;
  const avgLng = stations.reduce((acc, station) => acc + station.location.lng, 0) / stations.length;
  const center = { lat: avgLat, lng: avgLng } as Loc;
  return center;
};
